<template>
  <el-dialog
    v-model="arrayVisible"
    title="()"
    width="600px"
    @open="onOpen"
    @close="onClose"
  >
    <el-form
      ref="elFormRef"
      :model="formData"
      :rules="rules"
      size="small"
      label-width="120px"
    >
      <el-form-item label="" prop="minItems">
        <el-input v-model="formData.minItems" clearable></el-input>
      </el-form-item>
      <el-form-item label="" prop="maxItems">
        <el-input v-model="formData.maxItems" clearable></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="close"></el-button>
      <el-button type="primary" @click="handleConfirm"></el-button>
    </template>
  </el-dialog>
</template>

<script>
import { ref, reactive, onMounted, toRefs } from "vue";
import { validateSchemaData } from "../js/json-schema";
import { ADVANCED_SETTING_FORMAT } from "../js/schema-constant";
import {
  isUndefined,
  resetForm,
  submitRulesForm,
} from "../../../utils/validate";
import _ from "lodash";
import bus from "../../../utils/mitt-bus";
import { ElDialog, ElInput, ElButton, ElForm, ElFormItem } from "element-plus";

export default {
  name: "ArrayDialog",
  components: {
    ElDialog,
    ElInput,
    ElButton,
    ElForm,
    ElFormItem,
  },
  props: {
    visible: { type: Boolean, default: false }, // 
    initData: { type: Object, default: () => {} }, // 
  },
  emits: ["update:visible"],
  setup(props, context) {
    // ******************************************************************************************
    const elFormRef = ref(null); // dom
    // TODO()
    let data = reactive({
      arrayVisible: false,
      enableEnum: false, // 
      formData: { minItems: "", maxItems: "" }, // 
      rules: {
        minItems: [
          { validator: validateSchemaData, trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              if (parseInt(value) > parseInt(data.formData.maxLength)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        maxItems: [
          { validator: validateSchemaData, trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              if (parseFloat(value) < parseFloat(data.formData.minItems)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
      }, // 
      formatOptions: ADVANCED_SETTING_FORMAT, // 
    });
    // ******************************************************************************************
    onMounted(() => {
      data.arrayVisible = props.visible;
    });
    // ******************************************************************************************
    // TODO()
    const onOpen = () => {
      const { minItems, maxItems } = props.initData;
      if (isUndefined(minItems)) {
        data.formData.minItems = "";
      } else {
        data.formData.minItems = minItems;
      }

      if (isUndefined(maxItems)) {
        data.formData.maxItems = "";
      } else {
        data.formData.maxItems = maxItems;
      }
    };
    // TODO()
    const onClose = () => {
      if (elFormRef.value) {
        resetForm(elFormRef);
      }
      close();
    };

    // TODO()
    const handleConfirm = async () => {
      if (await submitRulesForm(elFormRef)) {
        const newData = _.cloneDeep(data.formData);
        bus.$emit(`schema-update-${props.initData.editorId}`, {
          eventType: "save-setting",
          ...props.initData, // 
          newData, // 
        });

        close();
      }
    };
    // TODO()
    const close = () => {
      context.emit("update:visible", false);
    };

    return {
      elFormRef,
      ...toRefs(data),
      onOpen,
      onClose,
      handleConfirm,
      close,
    };
  },
};
</script>

<style scoped lang="scss"></style>
