<template>
  <el-dialog
    v-model="numberVisible"
    :title="numberTitle"
    width="600px"
    @open="onOpen"
    @close="onClose"
  >
    <el-form
      ref="elFormRef"
      :model="formData"
      :rules="rules"
      size="small"
      label-width="100px"
      class="schema-dialog"
    >
      <div class="display-flex">
        <el-form-item label="" prop="minimum">
          <el-input v-model="formData.minimum" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="maximum" class="margin-left-xs">
          <el-input v-model="formData.maximum" clearable></el-input>
        </el-form-item>
      </div>
      <el-form-item label="" prop="format">
        <el-select v-model="formData.format" clearable multiple>
          <el-option
            v-for="(item, index) in formatOptions"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="enum">
        <template #label>
          <span>
            
            <el-checkbox
              v-model="enableEnum"
              label=""
              @change="formData.enum = ''"
            ></el-checkbox>
          </span>
        </template>
        <el-input
          v-model="formData.enum"
          placeholder=""
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 4 }"
          :disabled="!enableEnum"
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="close"></el-button>
      <el-button type="primary" @click="handleConfirm"></el-button>
    </template>
  </el-dialog>
</template>

<script>
import { ref, reactive, onMounted, toRefs } from "vue";
import {
  validateSchemaNumber,
  validateSchemaNumberEnum,
} from "../js/json-schema";
import { ADVANCED_SETTING_FORMAT } from "../js/schema-constant";
import {
  isEmpty,
  isUndefined,
  regIntegerNumber,
  resetForm,
  submitRulesForm,
} from "../../../utils/validate";
import _ from "lodash";
import bus from "../../../utils/mitt-bus";
import {
  ElCheckbox,
  ElDialog,
  ElInput,
  ElButton,
  ElForm,
  ElFormItem,
} from "element-plus";

export default {
  name: "NumberDialog",
  components: {
    ElCheckbox,
    ElDialog,
    ElInput,
    ElButton,
    ElForm,
    ElFormItem,
  },
  props: {
    visible: { type: Boolean, default: false }, // 
    initData: { type: Object, default: () => {} }, // 
  },
  emits: ["update:visible"],
  setup(props, context) {
    // ******************************************************************************************
    const elFormRef = ref(null); // dom
    // TODO()
    let data = reactive({
      numberVisible: false, // 
      enableEnum: false, // 
      numberTitle: "", // 
      formData: { minimum: "", maximum: "", enum: "", format: [] }, // 
      rules: {
        minimum: [
          {
            validator: (rule, value, callback) => {
              if (isEmpty(value)) {
                callback();
              }
              if (props.initData.type == "integer") {
                if (regIntegerNumber(value)) {
                  callback(new Error(""));
                }
              }
              if (parseFloat(value) > parseFloat(data.formData.maximum)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
          { validator: validateSchemaNumber, trigger: "blur" },
        ],
        maximum: [
          {
            validator: (rule, value, callback) => {
              if (isEmpty(value)) {
                callback();
              }
              if (props.initData.type == "integer") {
                if (regIntegerNumber(value)) {
                  callback(new Error(""));
                }
              }
              if (parseFloat(value) < parseFloat(data.formData.minimum)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
          { validator: validateSchemaNumber, trigger: "blur" },
        ],
        enum: [
          {
            validator: (rule, value, callback) => {
              if (data.enableEnum) {
                if (isEmpty(value)) {
                  callback(new Error(""));
                }
                if (props.initData.type == "integer") {
                  if (regIntegerNumber(value.replace(/\n/g, ""))) {
                    callback(new Error(""));
                  }
                }
              }
              callback();
            },
            trigger: "change",
          },
          { validator: validateSchemaNumberEnum, trigger: "blur" },
        ],
      }, // 
      formatOptions: ADVANCED_SETTING_FORMAT, // 
    });
    // ******************************************************************************************
    onMounted(() => {
      data.numberVisible = props.visible;
    });
    // ******************************************************************************************
    // TODO()
    const onOpen = () => {
      const { minimum, maximum, format, type } = props.initData;
      if (isUndefined(minimum)) {
        data.formData.minimum = "";
      } else {
        data.formData.minimum = minimum;
      }

      if (isUndefined(maximum)) {
        data.formData.maximum = "";
      } else {
        data.formData.maximum = maximum;
      }

      if (isUndefined(format)) {
        data.formData.format = [];
      } else {
        data.formData.format = format;
      }

      let enumValue = props.initData.enum;
      if (isUndefined(enumValue) || isEmpty(enumValue)) {
        data.formData.enum = "";
      } else {
        data.formData.enum = enumValue.join("\n");
        data.enableEnum = true;
      }

      if (type === "number") {
        data.numberTitle = "()";
      } else {
        data.numberTitle = "()";
      }
    };
    // TODO()
    const onClose = () => {
      if (elFormRef.value) {
        resetForm(elFormRef);
      }
      close();
    };

    // TODO()
    const handleConfirm = async () => {
      if (await submitRulesForm(elFormRef)) {
        const newData = _.cloneDeep(data.formData);

        if (newData.enum) {
          newData.enum = _.compact(newData.enum.split("\n"));
        }

        bus.$emit(`schema-update-${props.initData.editorId}`, {
          eventType: "save-setting",
          ...props.initData, // 
          newData, // 
        });

        close();
      }
    };
    // TODO()
    const close = () => {
      context.emit("update:visible", false);
    };

    return {
      elFormRef,
      ...toRefs(data),
      onOpen,
      onClose,
      handleConfirm,
      close,
    };
  },
};
</script>

<style scoped lang="scss">
.schema-dialog {
  :deep(.el-form-item) {
    margin-bottom: 25px;
  }
  :deep(.el-checkbox) {
    vertical-align: middle;
  }
  :deep(.el-checkbox__label) {
    padding-left: 0;
  }
  :deep(.el-form-item--small .el-form-item__label) {
    text-align: right;
  }
  :deep(.el-select--small) {
    width: 100%;
  }
}
</style>
