<template>
  <el-dialog
    v-model="stringVisible"
    title="()"
    width="600px"
    @open="onOpen"
    @close="onClose"
  >
    <el-form
      ref="elFormRef"
      :model="formData"
      :rules="rules"
      size="small"
      label-width="100px"
      class="schema-dialog"
    >
      <div class="display-flex">
        <el-form-item label="" prop="minLength">
          <el-input v-model="formData.minLength" clearable></el-input>
        </el-form-item>
        <el-form-item
          label=""
          prop="maxLength"
          class="margin-left-xs"
        >
          <el-input v-model="formData.maxLength" clearable></el-input>
        </el-form-item>
      </div>
      <el-form-item label="" prop="format">
        <el-select v-model="formData.format" clearable multiple>
          <el-option
            v-for="(item, index) in formatOptions"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="enum">
        <template #label>
          <span>
            
            <el-checkbox
              v-model="enableEnum"
              label=""
              @change="formData.enum = ''"
            ></el-checkbox>
          </span>
        </template>
        <el-input
          v-model="formData.enum"
          placeholder=""
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 4 }"
          :disabled="!enableEnum"
        ></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="close"></el-button>
      <el-button type="primary" @click="handleConfirm"></el-button>
    </template>
  </el-dialog>
</template>

<script>
import { ref, reactive, onMounted, toRefs } from "vue";
import {
  isEmpty,
  isUndefined,
  resetForm,
  submitRulesForm,
} from "../../../utils/validate";
import { ADVANCED_SETTING_FORMAT } from "../js/schema-constant";
import { validateSchemaData } from "../js/json-schema";
import _ from "lodash";
import bus from "../../../utils/mitt-bus";
import {
  ElDialog,
  ElInput,
  ElCheckbox,
  ElSelect,
  ElOption,
  ElButton,
  ElForm,
  ElFormItem,
} from "element-plus";

export default {
  name: "StringDialog",
  components: {
    ElDialog,
    ElInput,
    ElCheckbox,
    ElSelect,
    ElOption,
    ElButton,
    ElForm,
    ElFormItem,
  },
  props: {
    visible: { type: Boolean, default: false }, // 
    initData: { type: Object, default: () => {} }, // 
  },
  emits: ["update:visible"],
  setup(props, context) {
    // ******************************************************************************************
    const elFormRef = ref(null); // dom
    // TODO()
    let data = reactive({
      stringVisible: false,
      enableEnum: false, // 
      formData: { minLength: "", maxLength: "", enum: "", format: [] }, // 
      rules: {
        minLength: [
          { validator: validateSchemaData, trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              if (parseInt(value) > parseInt(data.formData.maxLength)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        maxLength: [
          { validator: validateSchemaData, trigger: "blur" },
          {
            validator: (rule, value, callback) => {
              if (parseInt(value) < parseInt(data.formData.minLength)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur",
          },
        ],
        enum: [
          {
            validator: (rule, value, callback) => {
              if (data.enableEnum && isEmpty(value.trim())) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "change",
          },
        ],
      }, // 
      formatOptions: ADVANCED_SETTING_FORMAT, // 
    });
    // ******************************************************************************************
    onMounted(() => {
      data.stringVisible = props.visible;
    });
    // ******************************************************************************************
    // TODO()
    const onOpen = () => {
      const { minLength, maxLength, format } = props.initData;
      if (isUndefined(minLength)) {
        data.formData.minLength = "";
      } else {
        data.formData.minLength = minLength;
      }

      if (isUndefined(maxLength)) {
        data.formData.maxLength = "";
      } else {
        data.formData.maxLength = maxLength;
      }

      if (isUndefined(format)) {
        data.formData.format = [];
      } else {
        data.formData.format = format;
      }

      let enumValue = props.initData.enum;
      if (isUndefined(enumValue) || isEmpty(enumValue)) {
        data.formData.enum = "";
      } else {
        data.formData.enum = enumValue.join("\n");
        data.enableEnum = true;
      }
    };
    // TODO()
    const onClose = () => {
      if (elFormRef.value) {
        resetForm(elFormRef);
      }
      close();
    };
    // TODO()
    const handleConfirm = async () => {
      if (await submitRulesForm(elFormRef)) {
        const newData = _.cloneDeep(data.formData);

        if (newData.enum) {
          newData.enum = _.compact(newData.enum.split("\n"));
        }

        bus.$emit(`schema-update-${props.initData.editorId}`, {
          eventType: "save-setting",
          ...props.initData, // 
          newData, // 
        });

        close();
      }
    };
    // TODO()
    const close = () => {
      context.emit("update:visible", false);
    };

    return {
      elFormRef,
      ...toRefs(data),
      onOpen,
      onClose,
      close,
      handleConfirm,
    };
  },
};
</script>

<style scoped lang="scss">
.schema-dialog {
  :deep(.el-checkbox) {
    vertical-align: middle;
  }
  :deep(.el-checkbox__label) {
    padding-left: 0;
  }
  :deep(.el-form-item--small .el-form-item__label) {
    text-align: right;
  }
  :deep(.el-select--small) {
    width: 100%;
  }
}
</style>
